import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import { useSelector } from 'react-redux';

import Home from './pages/Home';
import Profile from './pages/Profile';
import NotAuth from './pages/NotAuth';

import './App.css';

export default function App() {
  const loggedIn = useSelector(({ user }) => user.loggedIn);

  return (
    <Router>
      <Switch>
        <Route exact path="/login-us/">
          {loggedIn ? <Redirect to="/login-us/profile" /> : <Home />}
        </Route>
        <Route path="/login-us/profile" component={Profile} />
        <Route path="/login-us/not-auth" component={NotAuth} />
      </Switch>
    </Router>
  );
}
